@import 'colors';
@import 'shadow';

.outline-reading {
  display: block;
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  box-sizing: border-box;
  padding: 3em 4em;
  background-color: @white;
  overflow: auto;

  .outline-print {
    &__close {
      .shadow_light_regular();
      display: block;
      position: fixed;
      z-index: 3;
      top: 50%;
      right: 22px;
      margin: 0;
      width: 36px;
      height: 36px;
      border-radius: 18px;
      border: 1px solid @primary_border_color;
      line-height: 30px;
      text-align: center;
      transform: translateY(-50%);
      cursor: pointer;
      box-shadow: 0 0 8px @secondary_border_color;
      color: @fourth_text_color;
      background-color: @white;
      overflow: hidden;

      &:hover {
        background-color: @success_color;
        color: @white;
      }
    }

    h1,
    &__title {
      margin: 0.67em 0 !important;
      line-height: 1.3em;
      font-size: 3.5em;
    }
  }

  &_hidden {
    display: none !important;

    &.outline-print_sibling {
      display: none !important;

      &.wrapper {
        &.post {
          display: none;
        }
      }
    }
  }
}

@media (max-width: 480px) {
  .outline-reading {
    padding: 0 1.25em;

    .outline-print {
      &__close {
        top: 24px;
        right: 12px;
        width: 22px;
        height: 22px;
        line-height: 22px;
        border-radius: 11px;
        opacity: 0.8;
      }

      &__title {
        font-size: 2em;
      }
    }
  }
}
